import action from '../../../components/action/action.vue'
export default {
	components: {
		action
	},
	data() {
		return {
			font:'全部',
			Font:'全部',
			list:['等待执行','执行中','执行成功','执行失败','手动终止','自动终止','全部'],
			List:['全部'],
			cardid:'',
			plantype:'',
			datalist:'',
			index: '',
			Index:'',
			total:'',
		}
	},
	onLoad(e) {
		var data = new Date();
		var year = data.getFullYear();
		data.setMonth(data.getMonth() + 1, 1); //获取到当前月份,设置月份
		for (var i = 0; i < 6; i++) {
		  data.setMonth(data.getMonth() - 1); //每次循环一次 月份值减1
		  var m = data.getMonth() + 1;
		  m = m < 10 ? "0" + m : m;
		  this.List.push(data.getFullYear() + "-" + m);
		}
		this.cardid = e.cardid
		this.plantype = e.plantype
		this.getdata()
	},
	methods: {
		//状态
		// 获取数据
		getdata(){		
			this.$store.dispatch('quickList',{
				cardid:this.cardid,
				plantype:this.plantype,
				planstatus:this.index
			}).then(res=>{
				this.datalist = res.data
				this.total = res.total
			}).catch(err=>{})
		},
		screen(){
			this.$refs.action.actionConfig={
				list:this.list,
				type:0,
				isBorderColor:false,
				specClass: 'show'
			}
		},
		itemclick(index,type){
			this.font = this.list[index]
			if(index < 4){
				this.index = index	
			}else{
				if(index == 6){
					this.index = ''
				}else{
					this.index = index + 1
				}
			}
			this.getdata()
		},
		//时间
		sort(){
			this.$refs.Action.actionConfig={
				list:this.List,
				type:0,
				isBorderColor:false,
				specClass: 'show'
			}
		},
		itemClick(index,type){
			this.Font = this.List[index]
			if(index == 0){
				this.Index = ''
			}else{
				this.Index = this.Font
			}
			this.getdata()
		},
		link(e){
			uni.navigateTo({
				url:'transaction-detail?id='+e
			})
		}
	}
}
